import React, { Component } from "react";

import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../../components/helpers/field";
import { getTglSystem } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../../Validasi/ValidasiTanggalLaporan";
import LaporanExelBatalJual from "./exel/LaporanExelBatalJual";
// import LaporanBatalServiceExel from "./exel/LaporanBatalServiceExel";
import ExportBatalPenjualan from "./pdf/ExportBatalPenjualan";

class HeadFormLaporanBatalPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    // this.handleSubmit = this.handleSubmit.bind(this);
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  exportPdf() {
    ExportBatalPenjualan(this.props.LaporanDataPenjualan);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  render() {
    let databarang = [];
    let berat = 0;
    let ongkos = 0;
    let harga_jual = 0;
    let harga_total = 0;
    let harga_gram = 0;
    this.props.LaporanDataPenjualan.forEach((element) => {
      element.detail.forEach((detail) => {
        databarang.push(detail);
        berat = berat + detail.berat
        ongkos = ongkos + detail.ongkos
        harga_jual = harga_jual + detail.harga_jual
        harga_total = harga_total + detail.harga_total
        harga_gram = harga_gram + detail.harga_gram
      });
    });
    const columns = [
  
      {
        dataField: "kode_dept",
        text: "Kode Dept",
        footerAttrs: {
          colSpan: "3",
        },
        footer: (cell, row, num, index) => {
          return <div>Total Barang  : {databarang.length} </div>;
        },
      },
     
      {
        dataField: "kode_barcode",
        text: "Kode Barcode",
      },

      {
        dataField: "nama_barang",
        text: "Nama Barang",
      },
     
      {
        dataField: "berat",
        text: "Berat",
        footer: (cell, row, num, index) => {
          return <div>{berat.toFixed(3)} </div>;
        },
        formatter: (cell) => {
          return cell.toFixed(3);
        },
      },
      {
        dataField: "ongkos",
        text: "Ongkos",
        headerClasses: 'text-right',
        footer: (cell, row, num, index) => {
          return <div className="text-right">{ongkos.toLocaleString('ID-id')} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right">{cell.toLocaleString('ID-id')}</div>;
        },
      },
      {
        dataField: "harga_jual",
        text: "Harga Jual",
        headerClasses: 'text-right',
        footer: (cell, row, num, index) => {
          return <div className="text-right">{harga_jual.toLocaleString('ID-id')} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right">{cell.toLocaleString('ID-id')}</div>;
        },
      },
      {
        dataField: "harga_gram",
        text: "Harga/Gram",
        headerClasses: 'text-right',
        footer: (cell, row, num, index) => {
          return <div className="text-right">{harga_gram.toLocaleString('ID-id')} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right">{cell.toLocaleString('ID-id')}</div>;
        },
      },
      {
        dataField: "harga_total",
        text: "Harga Total",
        headerClasses: 'text-right',
        footer: (cell, row, num, index) => {
          return <div className="text-right">{harga_total.toLocaleString('ID-id')} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right">{cell.toLocaleString('ID-id')}</div>;
        },
      }
    ];
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>
          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_barcode"
              data={this.props.export ? databarang  :  []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Batal Penjualan Kosong"
            />
          </div>
          
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanExelBatalJual data={this.props.LaporanDataPenjualan} />
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

HeadFormLaporanBatalPenjualan = reduxForm({
  form: "HeadFormLaporanBatalPenjualan",
    enableReinitialize: true,
  validate: ValidasiTanggalLaporan 
})(HeadFormLaporanBatalPenjualan);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFormLaporanBatalPenjualan);


